/* eslint-disable react/prop-types */
import React from "react";
import { Link } from "react-router-dom";
import "./modal.scss";
// eslint-disable-next-line react/prop-types
function Modal({
  openModal,
  closeModal,
  setPrevModalOpen,
  setBlur,
  header,
  suspectData,
  suspectId,
  url,
}) {
  function selectAgain() {
    setPrevModalOpen(true);
    closeModal();
    setBlur(true);
  }
  return (
    //   openModal  .
    <div className={openModal ? "openModal modal" : "modal"}>
      {openModal ? (
        <section>
          <header>
            {header}
            <button className="close" onClick={closeModal}>
              &times;
            </button>
          </header>
          <main>
            <div className="subject">
              <img src={url} alt="subject" />
            </div>
            <div className="subjectSelect">
              <Link
                to={"/detection/" + suspectId}
                state={{ suspectImgUrl: url, suspectData: suspectData }}
              >
                
              </Link>
              <button onClick={selectAgain}> </button>
            </div>
          </main>
        </section>
      ) : null}
    </div>
  );
}
export default Modal;
